(function() {
  'use strict';

  angular
    .module('yoGulpAngular')
    .controller('NewsController', NewsController);

  /** @ngInject */
  function NewsController(news) {
    var vm = this;
	vm.newsThings = [];

    activate();

    function activate() {
		getNewsThings();
	}

    function getNewsThings() {
      vm.newsThings = news.getNews();
    }	
	
  }
})();

(function() {
  'use strict';

  angular
    .module('yoGulpAngular')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(menuEntries, news) {
    var vm = this;
	vm.myInterval = 5000;
	vm.noWrapSlides = false;
	var slides = vm.slides = [];
	var currIndex = 0;
    vm.awesomeThings = [];
	vm.newsThings = [];


    activate();

    function activate() {
		getAwesomeThings();
		getNewsThings();
		for (var i = 0; i < 4; i++) {
			addSlide();
		}
	}

    function addSlide() {
		//var newWidth = 600 + slides.length + 1;
		slides.push({
			//image: 'http://lorempixel.com/' + newWidth + '/300',
			image: ['assets/images/Cartell Martina final.jpg','assets/images/IMG_20150725_194545.jpg','assets/images/IMG_20150725_194829.jpg','assets/images/LOGO MANS.jpg'][slides.length % 4],
			text: ['Nice image','Awesome photograph','That is so cool','I love that'][slides.length % 4],
			id: currIndex++
		});
    }	

    function getAwesomeThings() {
      vm.awesomeThings = menuEntries.getMenuEntries();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
	
    function getNewsThings() {
      vm.newsThings = news.getNews();
    }	
	
  }
})();

